
import { Box , Image,Heading,Link ,Text, Flex, Grid} from '@chakra-ui/react'
import React from 'react'

const Projects = () => {
  return (
    <Box id='projects' bg={"#162a33"}
    color={"white"}
    minH={"100vh"}>
      <Heading>My projects</Heading>
    <Grid>
      <Box className='project-card'>
         <Image src='' />
         <Heading size={'md'} className='project-title'></Heading>
         <Text className='project-description'></Text>
         <Box className='project-tech-stack'></Box>
         <Link className='project-github-link'></Link>
         <Link className='project-deployed-link'></Link>
      </Box> 
      <Box className='project-card'>
         <Image src='' />
         <Heading size={'md'} className='project-title'></Heading>
         <Text className='project-description'></Text>
         <Box className='project-tech-stack'></Box>
         <Link className='project-github-link'></Link>
         <Link className='project-deployed-link'></Link>
      </Box>  
    </Grid>
    <Box className='react-activity-calender'>
       <Box>
          <Heading>Github Calender</Heading>
       </Box>
       <Flex>
          <Box>
            <Image id='github-streak-stats' src=''/>
          </Box>
          <Box>
            <Image id='github-stats-card' src=''/>
          </Box>
          <Box>
            <Image id='github-top-langs' src=''/>
          </Box>
       </Flex>
    </Box>
 </Box>
  )
}

export default Projects